.nav-tree .tree>ul>li{list-style-type:none;width:100%;position:relative;padding-left:0;line-height:40px
}
.nav-tree .tree>ul>li.active:not(.x-navmenu-collapsable)>a{background-color:#f1faff
}
.nav-tree .tree>ul>li.active>a{color:#42b983;font-weight:700
}
.nav-tree .tree>ul>li a{display:block;height:100%;width:100%;padding:0 20px
}
.nav-tree .tree>ul>li a:hover{background-color:#f1faff
}
.nav-tree .tree>ul>li i{padding-right:8px
