}
.nav-tree .tree .sub-menu li{position:relative;list-style-type:none
}
.nav-tree .tree .sub-menu li.blue-header:not(.x-navmenu-collapsable)>a{background-color:#f1faff
}
.nav-tree .tree .sub-menu a{position:relative;display:block;width:100%;height:40px;padding-left:45px
}
.nav-tree .tree .sub-control{position:absolute;right:9px;top:15px;font-size:10px;color:#333;transform-origin:center center
}
.hide-sub-menu{height:0
}
.nav-tree .tree .blue-header:before,.nav-tree .tree .hover-header:before,.nav-tree .tree li:not(.x-navmenu-collapsable):hover:before,.nav-tree .tree li:not(.x-navmenu-collapsable).active:before{content:"";top:0;bottom:0;right:0;width:4px;position:absolute;background-color:#42b983;z-index:99
}
.test{height:30px;width:30px;background-color:#000;transition:height 1.1s ease-in-out
}
.slide-left-enter-active,.slide-left-leave-active{transition:all .5s ease-in-out
