.small-tree{position:absolute;display:block;float:left;width:50px;height:calc(100% - 46px);margin:0;border:0;padding:0;overflow:visible;box-shadow:0 0 35px 0 rgba(6,71,126,0.1);z-index:99;background:#fff;margin-top:50px
