.small-tree .child-detail{position:absolute;display:none;width:210px;top:0;left:260px;z-index:2000;max-height:600px;box-shadow:5px 5px 10px rgba(6,71,126,0.1);background-color:#fff
}
.small-tree .child-detail li{height:40px;line-height:40px
}
.small-tree .child-detail li a{padding:0.75rem 1rem;color:#333333;white-space:pre;transition:background-color 0.15s, color 0.15s, border-color 0.15s
}
.small-tree .child-detail li:hover{background-color:#f1faff;border-right:3px solid #008ff6
}



