
section.login .form{width:500px;height:400px;margin:0 auto;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)
}
section.login .form .icon i{transition:all 4s
}
section.login .form .icon i:hover{transform:rotate(1440deg)
}
section.login .form>p{height:50px;text-align:center;transition:all 0.4s
}
section.login .form>p.info{margin:10px;font-size:12px;height:20px;color:#7f8c8d
}
section.login .form>p.input:hover{color:#3aa373
}
section.login .form>p input{transition:all 0.4s;width:200px;padding:8px;border-bottom:1px solid #42b983
}
section.login .form>p input:focus{width:210px
}
section.login .form>p button{transition:all 0.4s;color:#7f8c8d;border:1px solid #42b983;border-radius:4px;width:60px;height:30px
}
section.login .form>p button:hover{background-color:#42b983;color:#FFF
}



