
.page{margin-top:0;border:1px solid #e0e0e0;border-top:none;padding:7px 0;width:100%;height:50px
}
.page ul{border-bottom:0;float:left
}
.page ul .page-point{background-color:transparent;float:left;padding:0 5px
}
.page ul li{border:none
}
.page .total{float:right;height:50px;line-height:36px;margin:0 10px
}
.page .total .x-page-input{width:4.5rem;border-radius:0.2rem;height:36px;text-align:center;margin:0 5px;float:left;box-sizing:border-box;border:1px solid #e0e0e0;border-radius:0.2rem;padding:0.375rem 0.75rem;background-color:#fff;outline:0;transition:border-color ease-in-out 0.15s
}
.page .total .x-page-goto-page{display:inline-block;margin-left:0.3rem;height:36px;border:1px solid #e0e0e0;border-radius:0.2rem;padding:0.375rem 1rem;color:#666;transition:color 0.15s, background-color 0.15s;background-color:#fff
}
.page .total .x-page-goto-page:hover{background-color:#f7f7f7
}
.page .total span{float:left
}
.page .curPage{float:left;height:36px;line-height:36px;padding-left:14px
}
.page .pagination{padding:0;margin:0;float:right;width:auto;display:inline-block
}
.page .pagination a{display:inline-block;margin:0 0.25rem;border:1px solid #e0e0e0;border-radius:0.2rem;padding:0.375rem 1rem;color:#666;transition:color 0.15s, background-color 0.15s;background-color:#f7f7f7;white-space:pre
}
.page .pagination a:hover{color:#0d8ff6
}
.page .pagination .icon-chevron-left{margin-right:5px
}
.page .pagination .icon-chevron-right{margin-left:5px
}
.pagination>.active>a,.pagination>.active>span,.pagination>.active>a:hover,.pagination>.active>span:hover,.pagination>.active>a:focus,.pagination>.active>span:focus{border-color:#66b2e8 !important;background-color:#fff !important;color:#0d8ff6 !important
}



